import 'whatwg-fetch'
import fetchJsonp from 'fetch-jsonp'
const json2Url = (params) => {
  let arr = []
  Object.keys(params).map(items => {
    arr.push(`${items} = ${params[items]}`)
  })
  return arr.join('&')
}
const request = (url, opts) => new Promise((resolve, reject) => {
  return fetch(url, opts).then(res => {
    if (res.ok) {
      resolve(res.json())
    } else {
      reject(res)
    }
  })
})
const reqHeaders = {
  Accept: 'text/json'
}
export default {
  get (url, params) {
    return request(url + `?${json2Url(params)}`, {
      method: 'GET',
      headers: reqHeaders
    })
  },
  post (url, params) {
    return request(url, {
      method: 'POST',
      headers: Object.assign({}, reqHeaders, {
        'Content-Type': 'application/x-www-form-urlencoded'
      }),
      body: json2Url(params)
    })
  },
  jsonp (url, params) {
    return fetchJsonp(url + `?${json2Url(params)}`, {
      jsonpCallback: 'custom_callback'
    }).then(res => res.json())
  }
}



