// import http from '@/util/fetcher'
import list from '@/mock/mock'
export default {
  namespace: true,
  state: {
    data: [],
    loading: false,
    conditions: {
      pageSize: 5,
      pageNum: 1
    },
    emptyTxt: '请求有误'
  },
  mutations: {
    GETCONT (state, list) {
      state.data = list
    },
    GETSTATUS (state) {
      state.loading = !state.loading
    },
    GETPAGE (state, page) {
      state.conditions = Object.assign({}, this.state.conditions, page)
    },
    EMPTYTEXT (state, txt) {
      state.emptyTxt = txt
    }
  },
  actions: {
    getCont ({commit, add}) {
      // let {conditions} = state
      // http.post('/get/data', conditions).then(res => {
      //   commit('GETCONT', res.data)
      //   console.log(res.data)
      // })
      try {
        commit('GETCONT', list)
        commit('GETSTATUS')
        setTimeout(function () {
          commit('GETSTATUS')
        }, 2000)
      } catch (e) {
        setTimeout(function () {
          commit('GETSTATUS')
        }, 2000)
        commit('EMPTYTEXT')
      }
    }
  }
}



