<template>
  <div class="hello">
    <div id="particles"></div>
      <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <el-form-item label="账号" prop="user">
          <el-input type="text" v-model="ruleForm2.user" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
          <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>
<script>
export default {
  data () {
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('年龄不能为空'))
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('请输入数字值'))
        } else {
          if (value < 18) {
            callback(new Error('必须年满18岁'))
          } else {
            callback()
          }
        }
      }, 1000)
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入账号'))
      } else {
        if (this.ruleForm2.pass !== '') {
          this.$refs.ruleForm2.validateField('pass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }
    return {
      ruleForm2: {
        user: '',
        pass: ''
      },
      rules2: {
        user: [
          { validator: validatePass, trigger: 'blur' }
        ],
        pass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        age: [
          { validator: checkAge, trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    particlesJS.load('particles', '../../../static/data/particles.json')
  },
  methods: {
    submitForm () {
      this.$refs['ruleForm2'].validate((valid) => {
        if (valid) {
          this.$http.jsonp('/api/login', this.ruleForm2).then(json => {
            console.log(json)
          })
          this.$router.push('/home')
          // {{this.$store.state.count}} this.$store.dispatch('addNum')
        } else {
          console.log('error')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>
<style>
body,html{
  width:100%;
  height:100%;
}
*{
  padding:0;
  margin:0;
  list-style:none;
}
.hello {
  width:100%;
  height:100%;
  background-image: url("../../assets/bg.jpg");
  background-repeat:no-repeat;
  background-size:100% 100%;
}
.demo-ruleForm{
  position:absolute;
  top:40%;
  left:40%;
  transform:translate(-40%,-40%);
  position:relative;
  background:rgba(0,0,0,.4);
}
.el-button{
  width:80px;
  height:24px;
  background:#fff;
  border:none;
  padding:0 10px;
  margin-top:10px;
}
.el-form-item{
  width:100%;
  height:50px;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
}
.el-form-item__label{
  color:#fff;
}
#particles{
      position: absolute;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50% 50%;
}
</style>



