<template>
  <div class="header">
    <el-header>
      <span>惠答{{this.$store.state.num}}</span>
      <i class='el-icon-tickets' @click='setCollapse'></i>
      <span @click='clickBtn'>欢迎你，伯利亚！</span>
    </el-header>
  </div>
</template>
<script>
import bus from '@/util/bus'
import { mapActions } from 'vuex'
export default {
  name: '',
  methods: {
    ...mapActions({
      clickBtn: 'addNum'
    }),
    setCollapse () {
      bus.$emit('setCollapse')
    }
  }
}
</script>
<style scoped>
  i{
    line-height: 60px;
  }
  .el-header {
    display:flex;
    justify-content:space-between;
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
    background:#3a76e5;
  }
 .el-header span{
   color:#fff;
 }
</style>



