<template>
  <div id="home">
    <el-container class="el_container" style="display: flex;flex-direction:column;">
      <Header></Header>
      <el-container class="cont">
        <Aside></Aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import Header from '../header/header';
import Aside from '../aside/aside';
export default {
  components: {
    Header,
    Aside
  }
}
</script>
<style>
  html,body,#home,section{
      width:100%;
      height:100%;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
  }
  .el-menu-vertical-demo{
      background-color: #E9EEF3;
  }
  body > .el-container {
    margin-bottom: 40px;
    display:flex;
  }
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
</style>



