<template>
 <div class="box">
   <h3>{{this.$store.state.navTitle}}</h3>
   <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="推荐问答" name="first">
      <div class="table">
     <el-table
      :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      style="width: 100%"
      v-loading="loading"
      :empty-text="txt"
      >
      <el-table-column v-for="(val, key) in tabKey"
        :prop="key"
        :label="val[0]"
        :width="val[1]"
        :key="key"
       >
      </el-table-column>
    </el-table>
   </div>
    </el-tab-pane>
    <el-tab-pane label="全部问答" name="second">
       <Right></Right>
    </el-tab-pane>
  </el-tabs>
  <div class="block">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage2"
      :page-sizes="[2, 4, 3,6]"
      :page-size="5"
      layout="sizes, prev, pager, next"
      :total="5"
      >
    </el-pagination>
  </div>
 </div>
</template>
<script>
import Right from '../rate/rate'
import { mapActions, mapState } from 'vuex'
import { tabKey } from '../../util/tabkey'
export default {
  name: 'conManage',
  data () {
    return {
      pagesize: 4,
      currentPage1: 2,
      currentPage2: 4,
      currentPage3: 3,
      currentPage4: 6,
      activeName: 'first',
      stripe: true,
      currentPage: 1,
      num: 0,
      tabKey
    }
  },
  computed: {
    ...mapState({
      tableData: state => state.conManage.data,
      loading: state => state.conManage.loading,
      txt: state => state.conManage.emptyTxt
    })
  },
  methods: {
    handleSizeChange (val) {
      this.pagesize = val
      // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      this.currentPage = val
      // console.log(`当前页: ${val}`)
    },
    handleClick (tab, event) {
      // console.log(tab, event)
    },
    ...mapActions({
      initData: 'getCont'
    })
  },
  mounted () {
    this.initData()
  },
  components: {
    Right
  }
}
</script>
<style>
.el-table__header-wrapper{
    height:40px;
}
.cell{
  height:40px;
  margin-bottom:20px;
  text-align:center;
}
</style>



