import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login/login'
import Home from '@/components/home/home'
import CheckPending from '@/components/child/checkPending'
import TeacherList from '@/components/child/teacherList'
Vue.use(Router)
const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        title: '登录'
      }
    },
    {
      path: '/home',
      component: Home,
      redirect: '/home/checkPending',
      children: [
        {
          path: '/home/checkPending',
          component: CheckPending,
          meta: {
            title: '问答'
          }
        },
        {
          path: '/home/teacherList',
          component: TeacherList,
          meta: {
            title: '导师'
          }
        },
        {
          path: '/home/withdraw',
          component: {
            template: '<div>asass</div>'
          },
          meta: {
            title: '提现'
          }
        },
        {
          path: '/home/userList',
          component: {
            template: '<div>0000</div>'
          }
        },
        {
          path: '/home/conManage',
          component: {
            template: '<div>4444</div>'
          }
        },
        {
          path: '/home/activity',
          component: {
            template: '<div>5555</div>'
          }
        }
      ]
    }
  ]
})
export default router
router.beforeEach((to, from, next) => {
  // let that = this
  setTimeout(function () {
    router.app.$store.dispatch('router', to.meta.title)
    document.title = router.app.$store.state.navTitle
  }, 0)
  next()
})



