<template>
  <div class="table">
    <el-table
      :data="rightData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      style="width: 100%"
      v-loading="loading"
      :empty-text="txt"
    >
      <el-table-column v-for="(val, key) in tabKey"
        :prop="key"
        :label="val[0]"
        :width="val[1]"
        :key="key"
       >
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { tabKey } from '../../util/tabkey'
import { mapState } from 'vuex'
export default {
  name: 'right',
  data () {
    return {
      pagesize: 4,
      currentPage1: 2,
      currentPage2: 4,
      currentPage3: 3,
      currentPage4: 6,
      activeName: 'first',
      stripe: true,
      currentPage: 1,
      num: 0,
      tabKey,
      rightData: []
    }
  },
  computed: {
    ...mapState({
      loading: state => state.conManage.loading,
      txt: state => state.conManage.emptyTxt
    })
  },
  methods: {
    handleSizeChange (val) {
      this.pagesize = val
      // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      this.currentPage = val
      // console.log(`当前页: ${val}`)
    },
    handleClick (tab, event) {
      // console.log(tab, event)
    }
  },
  mounted () {
    this.$http.get('http://39.106.164.80:8086/getUserList', {}).then(res => {
      this.rightData = res.data
    })
  }
}
</script>
<style>
.el-table__header-wrapper{
    height:40px;
}
.cell{
  height:40px;
  margin-bottom:20px;
  text-align:center;
}
</style>



