<template>
    <div>
      <el-aside width="auto">
            <el-menu
            default-active="1-4-1"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            :collapse="isCollapse"
            router
            >
            <el-submenu v-for="(val,index) in routes" :index="index+''" :key="index">
            <template slot="title">
                <i class="el-icon-location"></i>
                <span slot="title">{{val.menuName}}</span>
            </template>
            <el-menu-item-group v-for="(items,key) in val.childMenu" :index="key+''" :key="key">
                <el-menu-item
                :index="'/home/'+items.route.path">{{items.menuName}}</el-menu-item>
            </el-menu-item-group>
            </el-submenu>
        </el-menu>
        </el-aside>
    </div>
</template>
<script>
import routes from '../../assets/routes';
import bus from '@/util/bus';
export default {
  name: '',
  data () {
    return {
      isCollapse: false,
      routes: routes
    }
  },
  mounted () {
    bus.$on('setCollapse', () => {
      this.isCollapse = !this.isCollapse
    })
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    }
  }
}
</script>
<style scoped>
.el-aside {
    height:100%;
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
}
</style>



